import React from 'react';

import EmailTypeForm from './EmailTypeForm';
import DropLoader from '../../utils/DropLoader';
import ShowJsonText from '../../common/ShowJsonText';

class EmailTypePage extends React.Component {
  constructor(props) {
      super(props);
      this.state = {
        ...props,
        data: { address: ""}
      }
  }
  handleOnChange = (e) => {
    this.setState({
      data: {...this.state.data, [e.target.name]: e.target.value}
    });
  }

  handleClearForm = () => {
    var data = { address: "" };
    this.setState({ data: data });
  }

  handleDropFileInput = (data) => {
    this.setState({ data: data });
  }

  render() {
    return (
      <div className="row">
        <div className="col-md-6 col-md-offset-2 well">
          <EmailTypeForm
            handleOnChange={this.handleOnChange}
            handleClearForm={this.handleClearForm}
          {...this.state} />
        </div>
        <div className="col-md-3 well">
          <ShowJsonText value={this.state.data} />
        </div>
        <DropLoader onChange={this.handleDropFileInput}/>
      </div>
    );
  }
}
EmailTypePage.propTypes = {
  // userSignupRequest: React.PropTypes.func.isRequired,
  // addFlashMessage: React.PropTypes.func.isRequired
};

export default EmailTypePage;



