import React from 'react';

import PersonNameTypeForm from './PersonNameTypeForm';
import DropLoader from '../../utils/DropLoader';
import ShowJsonText from '../../common/ShowJsonText';

class PersonNameTypePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      isLoading: false,
      errors: {}
    }
  }
  handleOnChange = (e) => {
    this.setState({
      data: {
        ...this.state.data,
        [e.target.name]: e.target.value
      }
    });
  }

  handleClearForm = () => {
    var data = {
        language: 'en-US',
        formattedName: '',
        legal: '',
        given: '',
        preferred: '',
        middle: '',
        family: '',
        alias: '',
        formerFamily: '',
        preferredSalutationCode: '',
        generationAffixCode: '',
        qualificationAffixCode: '',
        titleAffixCode: '',
        initials: ''
      };
    this.setState({data: data});
  }

  handleDropFileInput = (data) => {
    this.setState({data: data});
  }

  render() {

    return (
      <div className="row">
        <div className="col-md-4 col-md-offset-4">
          <PersonNameTypeForm
            handleOnChange={this.handleOnChange}
            handleClearForm={this.handleClearForm}
            {...this.state}/>
        </div>
        <div className="col-md-3 well">
          <ShowJsonText value={this.state.data}/>
        </div>
        <DropLoader onChange={this.handleDropFileInput}/>
      </div>
    );
  }
}
PersonNameTypePage.propTypes = {
  // userSignupRequest: React.PropTypes.func.isRequired,
  // addFlashMessage: React.PropTypes.func.isRequired
};

export default PersonNameTypePage;



