import React from 'react';
import PropTypes from 'prop-types';

import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';
// import classnames from 'classnames';
// var resolvedSchema = require('json-schema-loader!../../../../bower_components/schema/common/json/person/PersonNameType.json');

//console.log(resolvedSchema);

import TextFieldGroup from '../../common/TextFieldGroup';
// import validateInput from '../../../server/shared/validations/signup'

class PersonNameTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      isLoading: false,
      errors: {}
    };
  }
  onChange = (e) => {
    this.props.handleOnChange(e);
  }

  onClickClearForm = (e) => {
    this.props.handleClearForm(e);
  }

  isValid = () => {
    const {errors, isValid} = validateInput(this.state);
    if (!isValid) {
      this.setState({errors});
    }
    return isValid;
  }

  onSubmit(e) {
    e.preventDefault();

    // if (this.isValid()) {
    //   this.setState({errors: {}, isLoading: true});
    //   this.setState({isLoading: false});
    // }
  }


  render() {
    const { isLoading} = this.state;

    // const options = Object.keys(timezones).map(function(val){
    //   return (
    //     <option key={timezones[val]} value={timezones[val]}>{val}</option>
    //   )
    // })
    return (
      <form onSubmit={this.onSubmit}>
        <h2>Person Name Type
        </h2>
        <p>[resolvedSchema.description goes here]</p>
        <TextFieldGroup label="Formatted Name" onChange={this.onChange} value={this.state.formattedName} field="formattedName"/>
        <TextFieldGroup label="Legal" onChange={this.onChange} value={this.state.legal} field="legal"/>
        <TextFieldGroup label="Given" onChange={this.onChange} value={this.state.given} field="given"/>
        <TextFieldGroup label="Preferred" onChange={this.onChange} value={this.state.preferred} field="preferred"/>
        <TextFieldGroup label="Middle" onChange={this.onChange} value={this.state.middle} field="middle"/>
        <TextFieldGroup label="Family" onChange={this.onChange} value={this.state.family} field="family"/>
        <TextFieldGroup label="Alias" onChange={this.onChange} value={this.state.alias} field="alias"/>
        <TextFieldGroup label="Former Family" onChange={this.onChange} value={this.state.formerFamily} field="formerFamily"/>
        <TextFieldGroup label="Preferred SalutationCode" onChange={this.onChange} value={this.state.preferredSalutationCode} field="preferredSalutationCode"/>
        <TextFieldGroup label="Generation Affix Code" onChange={this.onChange} value={this.state.generationAffixCode} field="generationAffixCode"/>
        <TextFieldGroup label="Qualification Affix Code" onChange={this.onChange} value={this.state.qualificationAffixCode} field="qualificationAffixCode"/>
        <TextFieldGroup label="Title Affix Code" onChange={this.onChange} value={this.state.titleAffixCode} field="titleAffixCode"/>
        <TextFieldGroup label="Initials" onChange={this.onChange} value={this.state.initials} field="initials"/> {/* <div className={classnames("form-group", {'has-error': errors.timezone})}>
          <label className="control-label">Timezone</label>
          <select
            className="form-control"
            name="timezone"
            onChange={this.onChange}
            value={this.state.timezone}
          >
            <option value="" disabled>Choose Your Timezone</option>
            {options}
          </select>
          {errors.timezone && <span className="help-block">{errors.timezone}</span>}
        </div> */}

        <div className="form-group">
          <button disabled={isLoading} onClick={this.onClickClearForm} className="btn btn-primary btn-sm">
            Clear Form
          </button>
        </div>
      </form>

    );
  }
}

PersonNameTypeForm.propTypes = {
  formattedName: PropTypes.string,
  legal: PropTypes.string,
  given: PropTypes.string,
  middle: PropTypes.string,
  family: PropTypes.string,
  alias: PropTypes.string,
  formerFamily: PropTypes.string,
  preferredSalutationCode: PropTypes.string,
  generationAffixCode: PropTypes.string,
  qualificationAffixCode: PropTypes.string,
  titleAffixCode: PropTypes.string,
  initials: PropTypes.string
}

function validateInput(data) {
  let errors = {};

  if (Validator.isEmpty(data.formattedName)) {
    errors.formattedName = 'This field is required';
  }
  // if (Validator.isEmpty(data.legal)) {
  //     errors.legal = 'This field is required';
  // }
  // if (!Validator.isEmpty(data.given)) {
  //     errors.given = 'Given is invalid';
  // }
  // if (Validator.isEmpty(data.password)) {
  //     errors.password = 'This field is required';
  // }
  // if (Validator.isEmpty(data.passwordConfirmation)) {
  //     errors.passwordConfirmation = 'This field is required';
  // }
  // if (!Validator.equals(data.password, data.passwordConfirmation)) {
  //     errors.passwordConfirmation = 'Passwords must match';
  // }
  // if (Validator.isEmpty(data.timezone)) {
  //     errors.timezone = 'This field is required';
  // }
  return {errors, isValid: isEmpty(errors)};
}

export default PersonNameTypeForm;



