import React from 'react';


import Validator from 'validator';
import isEmpty from 'lodash/isEmpty';
import TextFieldGroup from '../../common/TextFieldGroup';
// var resolvedSchema = require('json-schema-loader!../../../../bower_components/schema/common/json/communication/EmailType.json');

// console.log(resolvedSchema);

class EmailTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...props,
      isLoading: false,
      errors: {}
    };
  }
  onChange = (e) => {
    this.props.handleOnChange(e);
  }

  onClickClearForm = (e) => {
    this.props.handleClearForm(e);
  }

  isValid = () => {
    const {errors, isValid} = validateInput(this.state);
    if (!isValid) {
      this.setState({errors});
    }
    return isValid;
  }

  onSubmit(e) {
    e.preventDefault();

    // if (this.isValid()) {
    //   this.setState({errors: {}, isLoading: true});
    //   this.setState({isLoading: false});
    // }
  }

  render() {
    let { isLoading, errors } = this.state;
    let { address } = this.props.data;

    return (
      <form onSubmit={this.onSubmit}>
        <h2>Email Type</h2>
        <p>[resolvedSchema.description goes here]</p>

        <TextFieldGroup label="Email Address"
          onChange={this.onChange}
          placeholder="name@example-domain.com" value={address} field="address" type="email"/>

        <div className="form-group">
          <button disabled={isLoading} onClick={this.onClickClearForm} className="btn btn-primary btn-sm">
            Clear Form
          </button>
        </div>
      </form>

    );
  }
}
function validateInput(data) {
  let errors = {};

  if (Validator.isEmpty(data.address)) {
    errors.address = 'This field is required';
  }

  return {errors, isValid: isEmpty(errors)};
}
export default EmailTypeForm;



